            notario: new Notario(),
        }),
        mounted() {
        },
        methods: {
            submit: function () {
                if (this.$refs.form.validate()) {
                    if (!this.modeEdit) {
                        this.create()
                    } else {
                        this.update();
                    }
                }
            },
            create() {
                axios.post(this.$urlApi.resourcesNotario,
                    this.notario
                ).then(response => {
                    this.notario = new Notario();
                    this.$toastr('success', '', 'TAREA REALIZADA CON XITO');
                    // this.dialog=false;
                    this.$nextTick(() => {
                        this.valid = true;
                    });
                    this.$emit('register-success');
                }).catch(errors => {
                    this.$notifyErrors(errors);
                });
            },
            update() {
                axios.put(this.$urlApi.resourcesNotario + '/' + this.notario.id,
                    this.notario
                ).then(response => {
                    Object.assign(this.editData, this.notario);
                    this.$toastr('success', '', 'TAREA REALIZADA CON XITO');
                    this.dialog = false;
                    this.$nextTick(() => {
                        this.notario = new Notario();
                        this.valid = true;
                    });
                    this.$emit('register-success');
                }).catch(errors => {
                    this.$notifyErrors(errors);
                });
            },
            openDialog() {
                Object.assign(this.notario, this.editData);
                this.dialog = true;
            }
        }
